/**
 * dialogify
 * https://github.com/OneupNetwork/dialogify
 */
(function($, window, dialogPolyfill, undefined){
    'use strict';

    var Dialogify = function(source, options){
        var dialog = window.document.createElement('dialog');
        dialogPolyfill.registerDialog(dialog);

        if (options == null || typeof options != 'object') {
            options = {};
        }

        this.options = options;

        var ajaxPrefix = options.ajaxPrefix === undefined ? '/ajax/' : options.ajaxPrefix;

        var self = this;
        var content;
        var ajax = false;
        if (source.charAt(0) == '#') {
            content = $(source).html();
        } else if(source.indexOf(ajaxPrefix) == 0){
            var loadingSvg = 'data:image/svg+xml;base64,';
            content = '<div class="dialogify-ajax-content"><img class="dialogify-ajax-loading" src="' + loadingSvg + '"></div>';
            ajax = true;
        } else {
            content = source;
        }

        content  = '<div class="dialogify__body">' + content + '</div>';

        var widthClass = options.size || 'dialogify__fixedwidth';
        var dialogClass = 'dialogify';
        if (options.fixed !== false) {
            dialogClass += ' fixed';
        }

        var dialogHtml = '<div class="dialogify__content ' + widthClass + '"><div></div></div>';

        if (options.useDialogForm !== false) {
            var edgeSubmitIssue = /(?:edge|iPad)/i.test(window.navigator.userAgent) ? ' onsubmit="return false;"' : '';
            dialogHtml = '<form method="dialog"' + edgeSubmitIssue + '>' + dialogHtml + '</form>';
        }

        this.id = 'dialogify_' + (++Dialogify.counter);

        $(dialog).attr('id', this.id)
            .addClass(dialogClass)
            .append(dialogHtml).append($closeButton)
            .on('close', function(e){
                $(self).triggerHandler('close');
                $(this).remove();
            })
            .on('cancel', function(e){
                $(self).triggerHandler('cancel');
                if (options.closable === false) {
                    e.preventDefault();
                }
            })
            .click(function(e){
                if (options.closable !== false && e.target == dialog) {
                    $(self).triggerHandler('cancel');
                    dialog.close();
                }
            })
            .appendTo('body');

        if (options.closable !== false) {
            var $closeButton = $('<a>').addClass('dialogify__close')
                .append('<img src="data:image/svg+xml;base64,">')
                .click(function(e){
                    $(dialog).triggerHandler('cancel');
                    $(dialog).triggerHandler('close');
                });

            $(dialog).append($closeButton);
        }

        this.$content = $(dialog).find('.dialogify__content > div');
        this.$content.append(content);

        if (ajax) {
            $.get(source, options.ajaxData || {}, function(resp){
                if (options.ajaxComplete) {
                    options.ajaxComplete.call(self);
                }

                self.$content.find('.dialogify-ajax-content').html(resp);
            }, 'html');
        }

        this.showModal = function(){
            dialog.showModal();
            $(this).triggerHandler('show');
        };

        this.show = function(){
            dialog.show();
            $(this).triggerHandler('show');
        };

        this.close = function(){
            dialog.close();
        };

        this.isOpen = function(){
            return dialog.open;
        };

        this.on = function(event, handler){
            $(this).on(event, handler);
            return this;
        };
    };

    Dialogify.prototype.title = function(title){
        var $titleBox = $('<h5>').addClass('dialogify_title')
            .append('<img src="data:image/svg+xml;base64,">')
            .append(title);

        if (this.$content.find('.dialogify_title').length) {
            this.$content.find('.dialogify_title').replaceWith($titleBox);
        } else {
            this.$content.prepend($titleBox);
        }

        return this;
    }

    Dialogify.prototype.buttons = function(buttons, options){
        if (!Array.isArray(buttons)) {
            buttons = [];
        }

        if (options == null || typeof options != 'object') {
            options = {};
        }

        this.$buttonList = {};

        var $buttonBox = $('<div>')
            .addClass('btn-box')
            .addClass(options.position || 'text-right');

        var self = this;
        for (var i = 0;i < buttons.length;i++){
            if (typeof buttons[i] == 'string') {
                var $btn = $(buttons[i]);
                var btnId = $btn.attr('id') || i;
                this.$buttonList[btnId] = $btn;
                $buttonBox.append($btn);
            } else {
                if (buttons[i] == null || typeof buttons[i] != 'object') {
                    buttons[i] = {};
                }

                var $btn = $('<button type="button">')
                    .addClass('btn btn-insert')
                    .addClass(buttons[i].type || '')
                    .data('click', buttons[i].click);

                if (buttons[i].type == Dialogify.BUTTON_PRIMARY && this.options.useDialogForm !== false) {
                    $btn.attr('type', 'submit');
                }

                if (buttons[i].focused) {
                    $btn.prop('autofocus', true);
                }

                if (buttons[i].disabled) {
                    $btn.disable();
                }

                $btn.text(buttons[i].text || '');
                $btn.click(function(e){
                    if (typeof $(this).data('click') == 'function') {
                        $(this).data('click').call(self, e);
                    }
                });

                this.$buttonList[buttons[i].id || i] = $btn;
                $buttonBox.append($btn);
            }
        }

        if (this.$content.find('.btn-box').length) {
            this.$content.find('.btn-box').replaceWith($buttonBox);
        } else {
            this.$content.append($buttonBox);
        }

        return this;
    }

    Dialogify.counter = 0;

    Dialogify.SIZE_LARGE = 'dialogify__autowidth';

    Dialogify.BUTTON_CENTER = 'text-center';
    Dialogify.BUTTON_LEFT = 'text-left';

    Dialogify.BUTTON_PRIMARY = 'btn-primary';
    Dialogify.BUTTON_DANGER = 'btn-danger';

    Dialogify.alert = function(message){
        new Dialogify('<p>' + message + '</p>', {closable: false})
            .buttons([{
                type: Dialogify.BUTTON_DANGER,
                click: function(){
                    this.close();
                }
            }], {position: Dialogify.BUTTON_CENTER})
            .showModal();
    };

    Dialogify.confirm = function(message, options){
        if (options == null || typeof options != 'object') {
            options = {};
        }

        new Dialogify('<p>' + message + '</p>')
            .buttons([
                {
                    text: '',
                    click: function(e){
                        options.cancel && options.cancel.call(this);
                        this.close();
                    }
                },
                {
                    text: '',
                    type: Dialogify.BUTTON_PRIMARY,
                    focused: true,
                    click: function(e){
                        options.ok && options.ok.call(this);
                        this.close();
                    }
                }
            ])
            .on('cancel', function(){
                options.cancel && options.cancel.call(this);
            })
            .showModal();
    };

    Dialogify.prompt = function(message, options){
        if (options == null || typeof options != 'object') {
            options = {};
        }

        var placeholder = options.placeholder ? ' placeholder="' + options.placeholder + '"' : '';
        var $html = $('<div>').html('<p>' + message + '</p>');
        $html.append('<input type="text" class="text-field dialogify-prompt-input"' + placeholder + '>');

        new Dialogify($html.html())
            .buttons([
                {
                    text: '',
                    click: function(e){
                        options.cancel && options.cancel.call(this);
                        this.close();
                    }
                },
                {
                    text: '',
                    type: Dialogify.BUTTON_PRIMARY,
                    click: function(e){
                        var value = this.$content.find('.dialogify-prompt-input').val();
                        options.ok && options.ok.call(this, value);
                        this.close();
                    }
                }
            ])
            .on('cancel', function(){
                options.cancel && options.cancel.call(this);
            })
            .showModal();
    };

    // inject css
    if (!$('#dialogifyCss').length) {
        $('<style>')
            .attr({type: 'text/css', id: 'dialogifyCss'})
            .html('.dialogify{display:none;position:absolute;top:50%;bottom:auto;left:50%;right:auto;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);margin:auto;max-width:1100px;min-width:360px;max-height:100vh;background-color:#fff;font-size:14px;color:#464646;border:none;border-radius:4px;overflow:auto;box-shadow:0 3px 4px rgba(0,0,0,0.1),0 1px 8px rgba(0,0,0,0.05),0 2px 20px rgba(0,0,0,0.1);z-index:2;padding:0;line-height:100%;font-family:"Helvetica Neue", Helvetica, Roboto, Arial, "Lucida Grande", "PingFang TC", "", "", Microsoft JhengHei, "-", sans-serif}.dialogify[open]{display:block}.dialogify.fixed{position:fixed;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.dialogify p{line-height:1.5}.dialogify.pixel-style:before{content:"";display:block;position:absolute;top:9px;bottom:9px;left:-9px;right:-9px;background:#FFF;z-index:-1}.dialogify.pixel-style:after{content:"";display:block;position:absolute;top:4px;bottom:4px;left:-5px;right:-5px;background-color:#FFF;background-image:url(../forum/box-corner.png);background-repeat:no-repeat;background-position:-17px 100%;z-index:-1}.dialogify>div{background-repeat:no-repeat;background-position:right bottom}.dialogify>div:after{content:"";position:absolute;bottom:0;left:0;width:20px;height:21px;-webkit-transform:rotate(90deg);transform:rotate(90deg);background-size:20px;background-repeat:no-repeat;opacity:0.7}.dialogify>div hr{border:none;height:1px;background:#E0E6E8}.dialogify .dialogify__body{min-height:40px;zoom:1}.dialogify .dialogify__body:after{clear:both;content:\'.\';display:block;width:0;height:0;visibility:hidden}.dialogify .dialogify__content{padding:22px 28px 20px 24px;box-sizing:border-box}.dialogify .dialogify__content-box{margin:20px auto}.dialogify .dialogify__maxheight{max-height:400px;overflow-y:scroll;overflow-x:hidden}.dialogify .dialogify__autowidth{max-width:1100px;min-width:560px}.dialogify .dialogify__fixedwidth{max-width:560px}.dialogify .dialog__maxheight{max-height:400px;overflow-y:auto}.dialogify .dialogify__close{position:absolute;top:8px;right:8px;color:#00555f}.dialogify .dialogify__close img{width:16px;height:16px;opacity:0.85}.dialogify .dialogify__close:hover{cursor:pointer}.dialogify .dialogify__close:hover img{opacity:1}.dialogify img.dialogify-ajax-loading{position:absolute;left:50%;top:50%;width:24px;height:24px;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.dialogify a:focus{outline:none;text-decoration:none}.dialogify .btn-danger{box-shadow:0 16px 26px -10px rgba(244,67,54,0.56),0 4px 25px 0px rgba(0,0,0,0.12),0 8px 10px -5px rgba(244,67,54,0.2)}.dialogify .btn-box{margin-top:20px;clear:both}.dialogify .btn-box .btn-insert{margin-left:8px;margin-right:3px}.dialogify .btn-box .check-group{float:left;margin-top:8px}.dialogify .btn-box .check-group input[type="checkbox"]+label h6{font-size:13px;color:#A6A6A6}.dialogify .btn-box .check-group input[type="checkbox"]+label a{color:#117e96}.dialogify .btn-box .check-group input[type="checkbox"]+label a:hover{color:#126e7d;text-decoration:none;transition:all 0.3s}.dialogify .btn-box .float-left{margin-left:0}.dialogify .btn-box .float-left a{margin-left:0}.dialogify .btn-box .post__text-small{font-weight:normal}.dialogify .link-form__actions{margin-top:10px;float:right;width:100%}.dialogify .btn{display:inline-block;border:1px solid transparent;border-radius:3px;padding:5px 12px;height:auto;font-size:14px;line-height:1.5;letter-spacing:1px;cursor:pointer;background-color:#e5e5e5;color:#A6A6A6;outline:none;text-decoration:none}.dialogify .btn:hover{background:#dcdcdc;color:#A6A6A6}.dialogify .btn.is-disabled{opacity:0.5;box-shadow:inset 0 1px 1px rgba(0,0,0,0.2);cursor:not-allowed !important}.dialogify .btn.is-disabled:hover{-webkit-animation:300ms shake;animation:300ms shake}@-webkit-keyframes shake{0%{-webkit-transform:rotate(1.5deg);transform:rotate(1.5deg)}50%{-webkit-transform:rotate(-2deg);transform:rotate(-2deg)}70%{-webkit-transform:rotate(1.5deg);transform:rotate(1.5deg)}100%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}}@keyframes shake{0%{-webkit-transform:rotate(1.5deg);transform:rotate(1.5deg)}50%{-webkit-transform:rotate(-2deg);transform:rotate(-2deg)}70%{-webkit-transform:rotate(1.5deg);transform:rotate(1.5deg)}100%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}}.dialogify .btn.btn-primary{background-color:#117e96;color:#FFF}.dialogify .btn.btn-primary:hover{background-color:#126e7d}.dialogify .btn.btn-danger{background-color:#f44336;color:#FFF}.dialogify .btn.btn-danger:hover{background:#DE2427}.dialogify .btn.btn-danger.is-disabled:hover{background-color:#f44336}.dialogify h5.dialogify_title{font-size:18px;color:#00555f;line-height:1;margin:0 0 18px;font-family:"Helvetica Neue", Helvetica, Roboto, Arial, "Lucida Grande", "PingFang TC", "", "Apple LiGothic Medium", "", sans-serif;font-weight:normal}.dialogify h5.dialogify_title img{width:12px;height:10px;position:relative;top:-2px;margin-right:3px}.dialogify h5.dialogify_title .post__text-small img{width:12px;height:12px;top:-1px}.dialogify hr{background:#E0E6E8;border:none;height:1px;width:100%;margin-bottom:15px}.dialogify .text-right{text-align:right}.dialogify .text-center{text-align:center}.dialogify .text-left{text-align:left}.dialogify .text-field{margin:10px 0;border:1px solid #D9D9D9;border-radius:3px;padding:8px;box-sizing:border-box;width:100%;background-color:#FFF;font-size:14px;color:#00555f}.dialogify .text-field:hover,.dialogify .text-field:focus{outline:none;border-color:#117e96}.dialogify .text-field::-webkit-input-placeholder,.dialogify .text-field:-moz-placeholder,.dialogify .text-field:-ms-input-placeholder,.dialogify .text-field::-moz-placeholder{color:#B3B3B3 !important}.dialogify .text-field.is-error{border-color:#DE2427;color:#DE2427}.dialogify textarea.text-field{resize:none;min-height:100px;line-height:1.5;font-size:14px;overflow:auto}dialog::-webkit-backdrop{background:#000;opacity:.7}dialog::backdrop{background:#000;opacity:.7}dialog+.backdrop{position:fixed;top:0;left:0;right:0;bottom:0;background:#000;opacity:.7}')
            .appendTo('head')
    }

    $.fn.extend({
        enable: function(){
            if ($(this).hasClass('btn btn-insert')) {
                $(this).prop('disabled', false).removeClass('is-disabled');
            }
        },
        disable: function(){
            if ($(this).hasClass('btn btn-insert')) {
                $(this).prop('disabled', true).addClass('is-disabled');
            }
        }
    });

    window.Dialogify = Dialogify;
})(jQuery, window, dialogPolyfill);
